@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&family=Roboto:wght@400;500&display=swap);
.skeleton-title {
    background-image:
      linear-gradient( 110deg, #e2e2e2, #f2f2f2 40%, #e1e1e1 );
    background-size: 400% 400%;
    width: 100%;
    height: 100%;
    min-height: 32px;
    animation: title 1.25s infinite;
  }
  @keyframes title { 
    0% { background-position: 0% 30% }
    50% { background-position: 100% 50% }
    100% { background-position: 0% 70% }
  }
  
  .skeleton:empty {
    margin: auto;
    width: 500px;
    height: 600px; /* change height to see repeat-y behavior */
    
    background-image:
      radial-gradient( circle 50px at 50px 50px, lightgray 99%, transparent 0 ),
      linear-gradient( 100deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0) 80% ),
      linear-gradient( lightgray 20px, transparent 0 ),
      linear-gradient( lightgray 20px, transparent 0 ),
      linear-gradient( lightgray 20px, transparent 0 ),
      linear-gradient( lightgray 20px, transparent 0 );
  
    background-repeat: repeat-y;
  
    background-size:
      100px 200px, /* circle */
      50px 200px, /* highlight */
      150px 200px,
      350px 200px,
      300px 200px,
      250px 200px;
  
    background-position:
      0 0, /* circle */
      0 0, /* highlight */
      120px 0,
      120px 40px,
      120px 80px,
      120px 120px;
  
    animation: shine 1s infinite;
  }
  
  @keyframes shine {
    to {
      background-position:
        100% 0, /* move highlight to right */
        0 0,
        120px 0,
        120px 40px,
        120px 80px,
        120px 120px;
    }
  }
.card-container {
  display: flex;
  flex-direction: column;
  background-color: white;
  border-radius: 0.4rem;
  width: 25vw;
  height: 65vh;
  box-shadow: 5px 5px 18px #ececec;
}

.section {
  display: flex;
  width: 100%;
  padding: 1rem;
}

.dialpad-container {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  width: 95%;
  flex-wrap: wrap;
}

#hero {
  height: 5rem;
}

#hero > h4 {
  font-size: 1.5rem;
  padding: 1.5rem;
  color: #484848;
}

#number-panel {
  height: 4rem;
  justify-content: flex-end;
  border-bottom: 1px solid #f5f5f5;
}

#number-panel > h2 {
  font-size: 2rem;
}

.digit-container {
  display: flex;
  padding: 1rem 2rem;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}

.number-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.digit {
  padding: 0.2rem;
  font-size: 1.3rem;
  font-weight: 600;
  color: #484848;
}

.chars {
  font-size: 1rem;
  font-weight: 300;
  color: #b3b3b3;
}

.clear-img {
  width: 35px;
  height: 21px;
  padding-left: 1rem;
  cursor: pointer;
}

.error{
  color: #FF3466;
  margin-top: 1rem;
}

@media (min-width: 768px) and (max-width: 1024px) {
  .card-container {
    width: 45vw;
    height: 78vh;
  }
}

@media only screen and (min-width: 481px) and (max-width:768px) {
  .card-container {
    width: 85vw;
    height: 75vh;
  }

}

/*Mobile Query*/
@media only screen and (max-width:480px) {
 .card-container {
    width: 85vw;
    height: 85vh;
  }

  .dialpad-container{
    width: 100%;
  }

}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-size: 1rem;
    font-family: 'Roboto', sans-serif;
}

.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url(/Lumiq.ai/static/media/pattern.d8a7d7cd.svg);
    background-repeat: no-repeat, repeat;
    width: 100vw;
    height: 98vh;
}

.title {
    font-size: 5rem;
    padding: 0.5rem 3rem;
    border-bottom: 1px solid #dedede;
}

.google-button {
    margin-top: 1rem;
}
